:host {
  display: flex;
  align-items: center;
}

:host .breadcrumb {
  display: inline-flex;
  align-items: center;
  color: var(--mdx-comp-breadcrumbs-color-default);
  font: var(--mdx-comp-breadcrumbs-font-default);
  letter-spacing: var(--mdx-comp-breadcrumbs-font-default-letter-spacing);
  text-transform: var(--mdx-comp-breadcrumbs-font-default-text-case);
  text-decoration: var(--mdx-comp-breadcrumbs-font-default-text-decoration);
  cursor: pointer;

  ::slotted(*) {
    max-width: 100px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &:hover {
    color: var(--mdx-comp-breadcrumbs-color-hover);
  }

  &__chevron-right {
    margin-left: var(--mdx-comp-breadcrumbs-spacing-gap-label-chevron);
    color: var(--mdx-comp-breadcrumbs-icon-color-chevron);
  }
}

:host([variant='current']) .breadcrumb {
  color: var(--mdx-comp-breadcrumbs-color-active);
  cursor: default;

  ::slotted(*) {
    max-width: none;
  }
}
