import { Component, h, Prop } from '@stencil/core';
import { ICON_SIZES } from '../icons/exports';
import { BREADCRUMB_VARIANTS } from './exports';

/**
 * @slot - For Label and Icon
 */
@Component({
  tag: 'mdx-breadcrumb',
  styleUrl: 'breadcrumb.scss',
  shadow: true,
})
export class Breadcrumb {
  @Prop()
  disabled = false;

  @Prop({ reflect: true })
  variant: BREADCRUMB_VARIANTS = BREADCRUMB_VARIANTS.DEFAULT;

  @Prop()
  href: string;

  @Prop()
  ariaLabel: string;

  @Prop()
  target: '_blank' | '_self' | '_parent' | '_top' = '_self';

  render() {
    return (
      <a class="breadcrumb" href={this.href} target={this.target} aria-label={this.ariaLabel} title={this.ariaLabel}>
        <slot></slot>
        {this.variant === BREADCRUMB_VARIANTS.DEFAULT ? (
          <mdx-icon-chevron-right class="breadcrumb__chevron-right" size={ICON_SIZES.XS}></mdx-icon-chevron-right>
        ) : null}
      </a>
    );
  }
}
